@media all and (max-width: 1050px) {
	
	.header {
		position: relative;
		
		.header__logo {
			//float: left;
			margin-right: 14px;
			width: 306px;
			height: 75px;
			//background: url(https://kp.ua/img/logo.png) 0 0 no-repeat;
			background-size: 306px;
		}
		
		.header__widgets {
			//overflow: hidden;
			padding: 9px 20px 0 9px;
			//font-size: 13px;
		}
		
		.header__mailto {
			//float: left;
			//padding: 6px 0 8px 0;
			margin-top: 6px;
			//border-top: 1px solid #bcc4cc;
			//border-bottom: 1px solid #bcc4cc;
			//color: #40454c;
			font-size: 0;
			line-height: 0;
			border: 0;
			//text-decoration: none;
			
			i {
				//display: inline-block;
				//margin-right: 10px;
				//vertical-align: middle;
				background: url('svg/mail.svg') 0 0 no-repeat;
				//display: none;
				width: 34px;
				height: 25px;
				background-size: 34px 25px;
				margin-right: 16px;
			}
		}
		
		.search {
			float: left;
			margin-top: 12px;
			padding: 0;
			background: transparent;
			//overflow: hidden;
		}
		
		.search__text {
			//float: left;
			//margin-right: -2px;
			padding: 0;
			//height: 28px;
			//border: 1px solid #bec5cd;
			//border-right: 0;
			height: 26px;
			
			width: 100%;
			max-width: 0;
			opacity: 0;
			transition: opacity .15s ease-in-out, max-width .15s ease-in-out, padding .15s ease-in-out;
			
			&.show-input {
				max-width: 163px;
				opacity: 1;
				padding: 0 10px;
			}
			
			&.text-bonus {
				//padding-left: ;
			}
			
		}
		
		.search__submit {
			//position: relative;
			//float: right;
			//width: 80px;
			height: 26px;
			line-height: 26px;
			//color: #3f464c;
			//font-size: 14px;
			//border: 1px solid #bec5cd;
			//border-radius: 3px;
			//background: #fcfcfc;
			//background-image: linear-gradient(top, #fcfcfc 0%, #d6d7d9 100%);
			-ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr='#FAFAFA', endColorstr='#D7D8DA' ,GradientType=0)";
			background: linear-gradient(180deg, rgba(250, 250, 250, 1) 0%, rgba(215, 216, 218, 1) 100%);
			border-style: Solid;
			border-color: #BEC5CD;
			border-width: 1px;
			border-radius: 3px;
			filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FAFAFA', endColorstr='#D7D8DA', GradientType=0);
			
			i {
				background: url('svg/search.svg') 0 0 no-repeat;
				width: 16px;
				height: 15px;
				background-size: 12px;
				margin: 0 2px 0 9px;
			}
			
			span {
				font-family: Arial;
				font-size: 12px;
				color: #4B5159;
				height: 22px;
				line-height: 20px;
				display: inline-block;
				vertical-align: middle;
			}
		}
		
		.header__newspaper {
			//position: relative;
			//float: left;
			//margin-top: -11px;
			padding: 3px 0 1px 9px;
			margin: 0;
			width: 138px;
			//background: #efefef;
			//font-size: 12px;
			//color: #989898;
			//text-decoration: none;
			font-family: Arial;
			font-size: 11px;
			color: #989898;
			
			strong {
				font-weight: bold;
				font-size: 12px;
				color: #40454D;
			}
		}
		
		.header__links {
			position: absolute;
			top: 53px;
			right: 7px;
			margin-bottom: 0;
			z-index: 0;
			opacity: 1;
			transition: opacity .1s ease-in-out;
			
			&.hide-links {
