				//padding: 0 9px;
				//height: 22px;
				//line-height: 22px;
				//text-decoration: none;
				////color: #fff;
				//border-radius: 5px 5px 0 0;
				border-radius: 3px;
				font-family: Arial;
				font-size: 12px;
				line-height: 22px;
				color: #FFFFFF;
				margin: 0 5px 5px 0;
				
				&.nav-spec__yellow {
					background: #ECA50D;
					box-shadow: none;
				}
				
			}
			
		}
		
		//hidden ellements 640
		
		.header__mob-links, .header__soc, .nav-top {
			display: none;
		}
		
		.header__links {
			i {
				display: none;
			}
		}
		
